"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { signOut, useSession } from "next-auth/react";
import { GET_CUSTOMER_INFO } from "../../queries/customer";
import { shopifyRequest } from "../../utils/shopify";

export default function AccountPage() {
  const router = useRouter();
  const { data: session, status } = useSession();
  const [customer, setCustomer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [isGoogleAuth, setIsGoogleAuth] = useState(false);

  useEffect(() => {
    // Only run check once status is determined
    if (status !== "loading") {
      checkAuthAndFetchData();
    }
  }, [status, session]);

  const checkAuthAndFetchData = async () => {
    const token = localStorage.getItem("shopify_customer_token");
    const expiresAt = localStorage.getItem("shopify_token_expires");
    const googleAuth = localStorage.getItem("google_auth");

    setIsGoogleAuth(!!googleAuth);

    // Check if user is authenticated via Google/OAuth
    if (status === "authenticated" && session?.user?.email) {
      console.log("Session user image:", session.user.image); // Debug log
      // Use OAuth session data
      setCustomer({
        firstName: session.user.name?.split(" ")[0] || "User",
        lastName: session.user.name?.split(" ").slice(1).join(" ") || "",
        displayName: session.user.name || "User",
        email: session.user.email,
        image: session.user.image || session.user.picture, // Try both image and picture
        createdAt: new Date().toISOString(),
        orders: { edges: [] }, // OAuth users won't have Shopify order history initially
      });
      setLoading(false);
      return;
    }

    // If not authenticated via OAuth, check Shopify token
    if (!token || (expiresAt && new Date(expiresAt) < new Date())) {
      // No valid authentication found, redirect to login
      localStorage.removeItem("shopify_customer_token");
      localStorage.removeItem("shopify_token_expires");
      localStorage.removeItem("customer_email");
      localStorage.removeItem("google_auth");
      router.replace("/login");
      return;
    }

    // Fetch Shopify customer data
    try {
      const { data } = await shopifyRequest(GET_CUSTOMER_INFO, {
        customerAccessToken: token,
      });
      if (data.customer) {
        setCustomer(data.customer);
      } else {
        handleLogout();
      }
    } catch (err) {
      console.error(err);
      setError("Failed to load account information");
    } finally {
      setLoading(false);
    }
  };

  const handleLogout = async () => {
    // Sign out from OAuth providers if using OAuth
    if (isGoogleAuth && status === "authenticated") {
      await signOut({ redirect: false });
    }

    // Clear all auth tokens
    localStorage.removeItem("google_auth");
    localStorage.removeItem("shopify_customer_token");
    localStorage.removeItem("shopify_token_expires");
    localStorage.removeItem("customer_email");

    router.replace("/login");
  };

  // Show loading state while checking authentication
  if (loading || status === "loading") {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#0a1833] mx-auto mb-4"></div>
          <div className="text-xl text-[#0a1833] font-medium">Loading...</div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-red-600 text-lg">{error}</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-40 px-4 sm:px-8 lg:px-12 text-[#0a1833]">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-white shadow-lg rounded-2xl p-8 mb-10 border border-gray-100">
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
            <div className="flex items-center gap-4">
              {/* Profile Picture - Show OAuth image or customer image */}
              {(customer?.image || session?.user?.image) && (
                <img
                  src={customer?.image || session?.user?.image}
                  alt="Profile"
                  className="w-16 h-16 rounded-full border-2 border-[#0a1833] object-cover"
                />
              )}
              <div>
                <h1 className="text-4xl font-serif font-semibold text-[#0a1833]">
                  Welcome back, {customer?.firstName}!
                </h1>
                <p className="text-gray-600 mt-2">{customer?.email}</p>
                {isGoogleAuth && (
                  <span className="inline-block mt-1 text-xs bg-blue-100 text-blue-800 px-2 py-1 rounded-full">
                    Signed in with {session?.user?.provider || "OAuth"}
                  </span>
                )}
              </div>
            </div>
            <button
              onClick={handleLogout}
              className="bg-[#0a1833] text-white px-6 py-2.5 rounded-lg font-medium hover:bg-[#142850] transition"
            >
              Logout
            </button>
          </div>
        </div>

        {/* Main Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Panel */}
          <div className="space-y-8">
            {/* Account Details */}
            <div className="bg-white rounded-2xl shadow-md p-8 border border-gray-100 hover:shadow-lg transition">
              <h2 className="text-2xl font-semibold mb-6 font-serif">
                Account Details
              </h2>
              <div className="space-y-4">
                <div>
                  <p className="text-sm text-gray-500">Name</p>
                  <p className="font-medium">{customer?.displayName}</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Email</p>
                  <p className="font-medium">{customer?.email}</p>
                </div>
                {customer?.phone && (
                  <div>
                    <p className="text-sm text-gray-500">Phone</p>
                    <p className="font-medium">{customer.phone}</p>
                  </div>
                )}
                <div>
                  <p className="text-sm text-gray-500">Member Since</p>
                  <p className="font-medium">
                    {new Date(customer?.createdAt).toLocaleDateString()}
                  </p>
                </div>
              </div>
            </div>

            {/* Default Address */}
            {customer?.defaultAddress && (
              <div className="bg-white rounded-2xl shadow-md p-8 border border-gray-100 hover:shadow-lg transition">
                <h2 className="text-2xl font-semibold mb-6 font-serif">
                  Default Address
                </h2>
                <div className="text-gray-700 leading-relaxed">
                  <p>
                    {customer.defaultAddress.firstName}{" "}
                    {customer.defaultAddress.lastName}
                  </p>
                  <p>{customer.defaultAddress.address1}</p>
                  {customer.defaultAddress.address2 && (
                    <p>{customer.defaultAddress.address2}</p>
                  )}
                  <p>
                    {customer.defaultAddress.city},{" "}
                    {customer.defaultAddress.province}{" "}
                    {customer.defaultAddress.zip}
                  </p>
                  <p>{customer.defaultAddress.country}</p>
                  {customer.defaultAddress.phone && (
                    <p>{customer.defaultAddress.phone}</p>
                  )}
                </div>
              </div>
            )}
          </div>

          {/* Right Panel - Orders */}
          <div className="lg:col-span-2">
            <div className="bg-white rounded-2xl shadow-md p-8 border border-gray-100">
              <h2 className="text-2xl font-semibold mb-6 font-serif">
                Order History
              </h2>

              {!customer?.orders?.edges ||
              customer.orders.edges.length === 0 ? (
                <div className="text-center py-12">
                  <svg
                    className="w-20 h-20 mx-auto text-gray-300 mb-4"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
                    />
                  </svg>
                  <p className="text-gray-600 text-lg">No orders yet</p>
                  <button
                    onClick={() => router.push("/")}
                    className="mt-4 bg-[#0a1833] text-white px-6 py-2.5 rounded-lg font-medium hover:bg-[#142850] transition"
                  >
                    Start Shopping
                  </button>
                </div>
              ) : (
                <div className="space-y-5">
                  {customer.orders.edges.map(({ node: order }) => (
                    <div
                      key={order.id}
                      className="border border-gray-100 rounded-xl p-5 hover:shadow-lg transition"
                    >
                      <div className="flex justify-between items-start mb-4">
                        <div>
                          <p className="font-semibold text-lg">
                            Order #{order.orderNumber}
                          </p>
                          <p className="text-sm text-gray-500">
                            {new Date(order.processedAt).toLocaleDateString()}
                          </p>
                        </div>
                        <div className="text-right">
                          <p className="font-semibold">
                            {order.totalPrice.currencyCode}{" "}
                            {parseFloat(order.totalPrice.amount).toFixed(2)}
                          </p>
                          <span
                            className={`text-xs px-3 py-1.5 rounded-full ${
                              order.fulfillmentStatus === "FULFILLED"
                                ? "bg-green-100 text-green-800"
                                : "bg-yellow-100 text-yellow-800"
                            }`}
                          >
                            {order.fulfillmentStatus || "Pending"}
                          </span>
                        </div>
                      </div>

                      {/* Order Items */}
                      <div className="space-y-3">
                        {order.lineItems.edges.map(({ node: item }) => (
                          <div
                            key={item.variant?.id}
                            className="flex items-center space-x-4 text-sm border-t border-gray-100 pt-3"
                          >
                            {item.variant?.image && (
                              <img
                                src={item.variant.image.url}
                                alt={item.variant.image.altText || item.title}
                                className="w-14 h-14 object-cover rounded-lg shadow-sm"
                              />
                            )}
                            <div className="flex-1">
                              <p className="font-medium text-[#0a1833]">
                                {item.title}
                              </p>
                              {item.variant?.title !== "Default Title" && (
                                <p className="text-gray-500">
                                  {item.variant?.title}
                                </p>
                              )}
                            </div>
                            <p className="text-gray-500">
                              Qty: {item.quantity}
                            </p>
                          </div>
                        ))}
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
